import React from 'react';
import { Table, TableHeader, TableColumn, TableBody, TableRow, TableCell, Chip } from '@heroui/react';
import { Branch } from '../types/statistics';

interface BranchTableProps {
  branches: Branch[];
}

export function BranchTable({ branches }: BranchTableProps) {
  return (
    <Table aria-label="Branch data table">
      <TableHeader>
        <TableColumn>NAMA RANTING</TableColumn>
        <TableColumn>STATUS SK</TableColumn>
        <TableColumn>TANGGAL BERAKHIR</TableColumn>
        <TableColumn>JUMLAH ANGGOTA</TableColumn>
      </TableHeader>
      <TableBody>
        {branches.map((branch) => (
          <TableRow key={branch.id}>
            <TableCell>{branch.name}</TableCell>
            <TableCell>
              <Chip
                color={branch.skStatus === 'active' ? 'success' : 'danger'}
                variant="flat"
              >
                {branch.skStatus === 'active' ? 'Aktif' : 'Kadaluarsa'}
              </Chip>
            </TableCell>
            <TableCell>{new Date(branch.skEndDate).toLocaleDateString('id-ID')}</TableCell>
            <TableCell>{branch.members}</TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
}